
html {
    font-size: 16px;
    font-family: 'Ubuntu', Arial, Helvetica, sans-serif;
    font-weight: 500;
    cursor: default;
    ///////////desktop//////////
    @media (min-width:769px) {
        font-size: 11px; }
    @media (min-width:1024px) {
        font-size: 16px; }
    @media (min-width:1300px) {
        font-size: 17px; }
    @media (min-width:1600px) {
        font-size: 18px; }
    @media (min-width:2100px) {
        font-size: 19px; }
    @media (min-width:2200px) {
        font-size: 22px; }
    @media (min-width:2560px) {
        font-size: 25px; }
    //tablet small
    @media (max-width:670px) {
        font-size: 12px; }

    @include phone {
        font-size: 14px; }
    @media (max-height: 600px) {
        font-size: 11px; } }




body {
    margin: 0;
    width: $viewport-width;
    background-color: #000; }

.wrapper {
    width: 100%;
    height: 100% !important;
    margin: 0 auto;
    overflow: hidden; }


.maincontent {
    float: left;
    width: 100%;
    margin: 0 auto; }

.clear {
  width: auto; }


.container {
    width: rem(940px);
    @include tablet {
        width: 100%; }
    margin: 0 auto;
    @extend %flex-center;
    flex-direction: column; }


.section {}

.section__title {
  font-family: 'Gagalin';
  font-size: rem(72px);
  font-weight: 400;
  line-height: 1.42;
  margin: 0; }


.order-link {
    padding: rem(10px) rem(16px) rem(7px) rem(17px);
    border-radius: rem(5px);
    background-color: $color-order;
    color: #fff;
    text-decoration: none;
    font-size: rem(13px);
    font-weight: 500;
    line-height: 1.42;
    text-transform: uppercase;
    transition: .5s;
    @include phone {
        font-size: rem(14px*$cof__font-size);
        padding: rem(8px*$cof__font-size) rem(13px*$cof__font-size); }

    &:hover {
        background-color: darken($color-order, 7%); } }
