
.page {
    &__arrow {
        position: absolute;
        bottom: rem(32px);
        left: 50%;
        transform: translateX(-50%);

        @include phone {
            bottom: 3vh; }
        &-img {
            width: 30px;
            height: 20px; } } }

.onepage-pagination {
    position: absolute;
    top: rem(96px);
    right: rem(53px);
    z-index: 5;
    list-style: none;
    margin: 0;
    padding: 0;
    @include tablet {
        display: none; }
    li {
        padding: 0;
        width: rem(6px);
        height: rem(6px);
        border-radius: 50%;
        background-color: #fff;
        margin-bottom: rem(15px);
        text-align: center;
        a {
            padding: rem(9px);
            width: rem(6px);
            height: rem(6px);
            display: block;
            margin-left: rem(-7px);
            position: relative;
            top: rem(-9px);
            left: rem(-2px);

            &:before {
                content: '';
                position: absolute;
                width: rem(6px);
                height: rem(6px);
                background: #fff;
                border-radius: 50%; }
            &:hover::before {
                width: rem(12px);
                height: rem(12px);
                background: none;
                border: 1px solid #fff;
                margin-top: rem(-3px);
                left: rem(6px); }
            &.active:before {
                width: rem(16px);
                height: rem(16px);
                background: none;
                border: 1px solid #fff;
                margin-top: rem(-5px);
                left: rem(4px); } } } }



















