    //         .food-close
    //             opacity: 1
    //     @include phone
 }    //         width: 100000vw




.menu__food--active {
    width: rem(627px);

    @include tablet {
        width: 67vw;
        .food-close {
            opacity: 1; } }
    @include phone {
        width: 100000vw; }
    .food-title {
        color: $color__orange-title; }
    .food-close {
        right: 30px; }
    .menu__food-img {
        transform: translate(-60%, -50%); } }
